.flexUser {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 20px;
  width: 1000px;
  padding: 20px;
}

.wrapper {
  padding: 10px;
}
.selectedPage {
  font-weight: bold;
  font-size: 20px;
  margin: 5px;
}
.img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
